import nextElementSibling from 'dom-next-element-sibling';
import previousElementSibling from 'dom-previous-element-sibling';
import rawObject from 'raw-object';

const directions = rawObject({
    left(element){
        let current = previousElementSibling(element);
        let [rect1, rect2] = getRects(element, current);

        if(isWest(rect1, rect2)){
            return current;
        }

        while(current = previousElementSibling(current)){
            let [rect2] = getRects(current);
            if(isWest(rect1, rect2)){
                return current;
            }
        }

        /*if(rect2.right <= rect1.left){
            if(rect2.top <= rect1.top){
                return current;
            }
        }

        while(current = previousElementSibling(current)){
            let [rect2] = getRects(current);
            if(rect2.right <= rect1.left){
                if(rect2.top <= rect1.top){
                    return current;
                }
            }
        }*/
    },
    up(element){


        let current = previousElementSibling(element);
        let [rect1, rect2] = getRects(element, current);

        if(isNorth(rect1, rect2)){
            return current;
        }

        while(current = previousElementSibling(current)){
            let [rect2] = getRects(current);
            if(isNorth(rect1, rect2)){
                return current;
            }
        }
    },
    right(element){

        let current = nextElementSibling(element);
        let [rect1, rect2] = getRects(element, current);

        if(isEast(rect1, rect2)){
            return current;
        }

        while(current = nextElementSibling(current)){
            let [rect2] = getRects(current);
            if(isEast(rect1, rect2)){
                return current;
            }
        }
    },
    down(element){

        let current = nextElementSibling(element);
        let [rect1, rect2] = getRects(element, current);

        if(isSouth(rect1, rect2)){
            return current;
        }

        while(current = nextElementSibling(current)){
            let [rect2] = getRects(current);
            if(isSouth(rect1, rect2)){
                return current;
            }
        }
    }
});

export default function domPick(element, direction, range = 0){
    return directions[direction](element, range);
}

function getRects(...elements){
    return elements.map(el=>el.getBoundingClientRect());
}

function isNorth(rect1, rect2){
    if(rect2.bottom <= rect1.top){
        return vReachable(rect1, rect2);
    }
    return false;
}

function isSouth(rect1, rect2){
    if(rect2.top >= rect1.bottom){
        return vReachable(rect1, rect2);
    }
    return false;
}

function vReachable(rect1, rect2){
    return (rect2.left <= rect1.left && rect2.right > rect1.left);
}

function isEast(rect1, rect2){
    if(rect2.left >= rect1.right){
        return hReachable(rect1, rect2);
    }
    return false;
}

function isWest(rect1, rect2){
    if(rect2.right <= rect1.left){
        return hReachable(rect1, rect2);
    }
    return false;
}

function hReachable(rect1, rect2){
    return (rect2.top <= rect1.top && rect2.bottom > rect1.top);
}
